<template>
  <div class="client-info-detail frame-container box align-default short">

    <!-- 补白 -->
    <div class="client-info-detail__padding"></div>

    <!-- 导航栏 -->
    <div class="client-info-detail__nav box plr-30 align-hor-bet">
      <div class="icon iconfont  iconreturn"
           @click="onBack">
      </div>
      <div class="title">
        {{merchantName}}
      </div>
      <div class="pass"></div>
    </div>

    <!-- 信息主体 -->
    <div class="client-info-detail__content box match-left-space">

      <div class="match-width box align-default">
        <div class="title">营业执照</div>
        <div class="item">
          <div class="subtitle">
            <span class="star">*</span>营业执照注册号
          </div>
          <div class="match-left-space align-right">
            <input placeholder="请输入内容" v-model="detail.license"/>
          </div>
        </div>
        <div class="item">
          <div class="subtitle">
            <span class="star">*</span>经营范围
          </div>
          <div class="match-left-space align-right">
            <input placeholder="请输入内容" v-model="detail.businessScope"/>
          </div>
        </div>
        <div class="item">
          <div class="subtitle">
            <span class="star">*</span>营业期限
          </div>
          <div class="match-left-space align-right">
            <div class="input align-default">
              <mu-date-input
                @change="onDateTimeChange(2, 1, $event)"
                v-model="detail.startBusinessTime"
                label="请输入开始时间"
                container="bottomSheet"
                label-float
                full-width>
              </mu-date-input>
              <mu-date-input
                @change="onDateTimeChange(2, 2, $event)"
                v-model="detail.endBusinessTime"
                label="请输入结束时间"
                container="bottomSheet"
                label-float
                full-width>
              </mu-date-input>
            </div>
          </div>
        </div>
        <div class="item">
          <div class="subtitle">
            <span class="star">*</span>营业执照照片
          </div>
          <div class="match-left-space align-right">
            <div class="match-left-space" @click="callTakePhoto(1)">&nbsp;</div>
            <div
              class="input align-right"
              style="width: auto">
              <img
                v-if="detail.businessLicensePhotoId"
                :src="detail.businessLicensePhotoId | loadImage"
                @click="previewImage(detail.businessLicensePhotoId)">
              <!-- <div v-else class="img"></div> -->
              <div class="icon iconfont iconenter ml-10" @click="callTakePhoto(1)"></div>
            </div>
          </div>
        </div>
      </div>

      <div class="match-width box align-default">
        <div class="title">企业法人/经办人</div>
        <div class="item">
          <div class="subtitle">
            <span class="star">*</span>证件持有人类型
          </div>
          <div class="match-left-space align-right">
            <div
              class="input align-right"
              @click="callActionSheet(1)">
              <div>{{detail.representativeType | representativeTypeFilter}}</div>
              <div class="icon iconfont iconenter ml-10"></div>
            </div>
          </div>
        </div>
        <div class="item">
          <div class="subtitle">
            <span class="star">*</span>证件持有人姓名
          </div>
          <div class="match-left-space align-right">
            <div class="input align-right">
              <input placeholder="请输入内容" v-model="detail.representativeName"/>
            </div>
          </div>
        </div>
        <div class="item">
          <div class="subtitle">
            <span class="star">*</span>证件类型
          </div>
          <div class="match-left-space align-right">
            <div
              class="input align-right"
              @click="callActionSheet(2)">
              <div>{{detail.certificate | certificateFilter}}</div>
              <div class="icon iconfont iconenter ml-10"></div>
            </div>
          </div>
        </div>
        <div class="item">
          <div class="subtitle">
            <span class="star">*</span>证件号码
          </div>
          <div class="match-left-space align-right">
            <input placeholder="请输入内容" v-model="detail.certificateNum"/>
          </div>
        </div>
        <div class="item">
          <div class="subtitle">
            <span class="star">*</span>证件有效期
          </div>
          <div class="match-left-space align-right">
            <div class="input align-default">
              <mu-date-input
                @change="onDateTimeChange(1, 1, $event)"
                v-model="detail.startCertificateTime"
                label="请输入开始时间"
                container="bottomSheet"
                label-float
                full-width>
              </mu-date-input>
              <mu-date-input
                @change="onDateTimeChange(1, 2, $event)"
                v-model="detail.endCertificateTime"
                label="请输入结束时间"
                container="bottomSheet"
                label-float
                full-width>
              </mu-date-input>
            </div>
          </div>
        </div>
        <div class="item">
          <div class="subtitle">
            <span class="star">&nbsp;&nbsp;</span>证件扫描件
          </div>
          <div class="match-left-space align-right">
            <div class="match-left-space" @click="callTakePhoto(2)">&nbsp;</div>
            <div
              class="input align-right"
              style="width: auto">
              <img
                v-if="detail.epresentativePhotoId"
                :src="detail.epresentativePhotoId | loadImage"
                @click="previewImage(detail.epresentativePhotoId)">
              <!-- <div v-else class="img"></div> -->
              <div class="icon iconfont iconenter ml-10" @click="callTakePhoto(2)"></div>
            </div>
          </div>
        </div>
        <div class="item" v-show="detail.certificate===1">
          <div class="subtitle">
            <span class="star">&nbsp;&nbsp;</span>证件扫描件
          </div>
          <div class="match-left-space align-right">
            <div class="match-left-space" @click="callTakePhoto(3)">&nbsp;</div>
            <div
              class="input align-right"
              style="width: auto">
              <img
                v-if="detail.epresentativePhotoId2"
                :src="detail.epresentativePhotoId2 | loadImage"
                @click="previewImage(detail.epresentativePhotoId2)">
              <!-- <div v-else class="img"></div> -->
              <div class="icon iconfont iconenter ml-10" @click="callTakePhoto(3)"></div>
            </div>
          </div>
        </div>

      </div>

      <div class="match-width box align-center">
        <div
          class="next box align-center"
          @click="onNext">
          下一步
        </div>
      </div>

      <!-- action-sheet -->
      <mu-bottom-sheet :open.sync="open">
        <div class="action-sheet box align-default">

          <div class="title box align-hor-bet plr-30">
            <div
              @click="onActionSheetClose">
              返回
            </div>
            <div
              class="confirm"
              @click="onActionSheetConfirm">
              确定
            </div>
          </div>

          <!-- 证件持有人类型 -->
          <div
            class="match-width"
            v-for="(item, index) in representativeTypes"
            :key="index"
            v-if="status == 1">
            <div
              :class="['item align-hor-bet plr-30 ptb-30', index==curRepresentativeType?'active':'']"
              @click="changeItem(index)">
              <div>{{item}}</div>
              <div
                v-if="index==curRepresentativeType"
                class="icon iconfont iconcheck">
              </div>
              <div class="pass" v-else></div>
            </div>
          </div>

          <!-- 证件类型 -->
          <div
            class="match-width"
            v-for="(item, index) in certificateTypes"
            :key="index"
            v-if="status == 2">
            <div
              :class="['item align-hor-bet plr-30 ptb-30', index==curCertificateType?'active':'']"
              @click="changeItem(index)">
              <div>{{item}}</div>
              <div
                v-if="index==curCertificateType"
                class="icon iconfont iconcheck">
              </div>
              <div class="pass" v-else></div>
            </div>
          </div>

          <!-- 补脚 -->
          <div class="action-sheet__padding"></div>

        </div>
      </mu-bottom-sheet>

      <!-- action-sheet -->
      <mu-bottom-sheet :open.sync="openMenu">
        <div class="action-sheet box align-default menu">

          <div
            class="item align-center"
            @click="onTakePhoto(1)">
            拍照
          </div>

          <div
            class="item align-center"
            @click="onTakePhoto(2)">
            从手机相册选择
          </div>

          <div
            class="item align-center cancel"
            @click="onActionMenuShow(2)">
            取消
          </div>

          <!-- 补脚 -->
          <div class="action-sheet__padding"></div>

        </div>
      </mu-bottom-sheet>
      <!-- 隐藏获取图片 -->
      <div class="file">
        <input type="file" ref="fileCamera" capture="camera" accept="image/*" @change="onFileChange"/>
        <div class="cover"></div>
      </div>
      <!-- 隐藏获取图片 -->
      <div class="file">
        <input type="file" ref="file" accept="image/*" @change="onFileChange"/>
        <div class="cover"></div>
      </div>

    </div>

    <!-- 图片预览 -->
    <div class="cover-x align-default" v-if="isImagePreview" @click="isImagePreview = false">
      <div class="match-left-space"></div>
      <div class="image-preview">
        <img class="match-parent" :src="imgUrl | previewLoadImage">
      </div>
      <div class="match-left-space"></div>
    </div>

  </div>
</template>

<script>
import { url } from '@/utils/src/request'
import { CLIENT_INFO_RECORD, CLIENT_INFO_RECORD_LAST } from '@/router/types'
import { clientInfoDetailIDLocal, clientInfoDetailLocal, clientInfoDetailMerchantNameLocal } from '@/storage'
import { clientInfoApi } from '@/api'

export default {
  data() {
    return {
      detail: {},
      open: false,
      status: 0,
      scenes: ['线下', '公众号/小程序', '网站', 'APP'],
      curScene: 0,
      majors: ['企业', '个体工商户'],
      curMajor: 0,
      appStates: ['已上线', '未上线'],
      curAppState: 0,
      openMenu: false,
      photoTaker: 0,
      representativeTypes: ['法人', '经办人'],
      curRepresentativeType: 0,
      certificateTypes: ['身份证', '护照'],
      curCertificateType: 0,
      isImagePreview: false,
      imgUrl: '',
      merchantName: ''
    }
  },

  methods: {
    // 返回
    onBack () {
      // TODO
      this.$router.push({
        name: CLIENT_INFO_RECORD
      })
    },
    // 唤醒action-sheet
    callActionSheet (status) {
      // TODO
      this.open = true
      this.status = +status
      // status
      // 1 证件持有人类型
      // 2 证件类型
      if (this.status === 1) {
        this.curRepresentativeType = this.transferRepType(this.detail.representativeType)
      } else if (this.status === 2) {
        this.curCertificateType = this.transferCerType(this.detail.certificate)
      }
    },
    // action-sheet取消
    onActionSheetClose () {
      this.open = false
    },
    // action-sheet确认
    onActionSheetConfirm () {
      this.onActionSheetClose()
      if (this.status === 1) {
        // 证件持有人类型
        this.detail.representativeType = (this.curRepresentativeType + 1)
      } else if (this.status === 2) {
        // 证件类型
        this.detail.certificate = (this.curCertificateType + 1)
      }
    },
    // 切换选项
    changeItem (index) {
      if (this.status === 1) {
        // 证件持有人类型
        this.curRepresentativeType = +index
      } else if (this.status === 2) {
        // 证件类型
        this.curCertificateType = +index
      }
    },
    // 控制actionSheetMenu显隐
    onActionMenuShow (status) {
      if (+status === 2) {
        this.openMenu = false
      } else if (+status === 1) {
        this.openMenu = true
      }
    },
    // 相机打开
    onTakePhoto (status) {
      if (+status === 1) {
        // 拍照
        this.$refs.fileCamera.click()
      } else if (+status === 2) {
        // 从相机打开
        this.$refs.file.click()
      }

      // console.log('相机', this.photoTaker, status)
      this.openMenu = false
    },
    // 获取图片
    callTakePhoto (index) {
      this.photoTaker = +index
      this.openMenu = true
      // index
      // 1  营业执照照片
      // 2  证件扫描件
    },
    // 下一步
    onNext () {
      let requiredData = ['license', 'businessScope', 'startBusinessTime', 'endBusinessTime', 'businessLicensePhotoId', 'representativeType', 'representativeName', 'certificate', 'certificateNum', 'startCertificateTime', 'endCertificateTime']
      let flag = true
      for (let i in this.detail) {
        if (this.detail.hasOwnProperty(i) && requiredData.indexOf(i) !== -1) {
          if (!this.detail[i]) {
            flag = false
            break
          }
        }
      }
      if (flag) {
        // console.log(this.detail)
        clientInfoDetailLocal.set(JSON.stringify(this.detail))
        // 跳转到下一个页面
        this.$router.push({
          name: CLIENT_INFO_RECORD_LAST
        })
      } else {
        this.$toast.error('有内容未填入')
      }
    },
    // 时间改变
    onDateTimeChange (status, index, e) {
      //    status         index
      // 1  证件有效期      开始时间
      // 2  营业期限        结束时间
      if (+status === 1) {
        let startTime = this.detail.startCertificateTime
        let endTime = this.detail.endCertificateTime
        if (startTime && endTime) {
          startTime = this.getTimestamp(startTime)
          endTime = this.getTimestamp(endTime)
          if (startTime >= endTime) {
            this.$toast.error('开始时间必须早于结束时间')
            this.detail.startCertificateTime = ''
            this.detail.endCertificateTime = ''
          } else {
            this.detail.startCertificateTime = this.getTime(this.detail.startCertificateTime)
            this.detail.endCertificateTime = this.getTime(this.detail.endCertificateTime)
          }
        }
      } else if (+status === 2) {
        let startTime = this.detail.startBusinessTime
        let endTime = this.detail.endBusinessTime
        if (startTime && endTime) {
          startTime = this.getTimestamp(startTime)
          endTime = this.getTimestamp(endTime)
          // console.log(startTime >= endTime)
          if (startTime >= endTime) {
            this.$toast.error('开始时间必须早于结束时间')
            this.detail.startBusinessTime = ''
            this.detail.endBusinessTime = ''
          } else {
            this.detail.startBusinessTime = this.getTime(this.detail.startBusinessTime)
            this.detail.endBusinessTime = this.getTime(this.detail.endBusinessTime)
          }
        }
      }
    },
    // 时间戳
    getTimestamp (str) {
      return str ? (new Date(Date.parse(str)).getTime()) : 0
    },
    // 获取年月日
    getTime (str) {
      if (str) {
        // let tmp = new Date(Date.parse(str)).toLocaleDateString()
        let date = new Date(Date.parse(str))
        let y = date.getFullYear()
        let m = date.getMonth() + 1
        if (m < 10) {
          m = '0' + m
        }
        let d = date.getDate()
        if (d < 10) {
          d = '0' + d
        }
        // tmp = tmp.split('/').join('-')
        let tmp = y + '-' + m + '-' + d
        return tmp
      }
      return ''
    },
    // 文件改变
    onFileChange (e) {
      if (!e.target.files) {
        return
      }
      let file = e.target.files[0]
      // let file = this.$refs.file.files[0]
      if (file) {
        clientInfoApi.uploadImage(file).then(res => {
          if (res.code === 200) {
            this.$toast.success('图片上传成功')
            let photoId = res.obj
            // 1  营业执照照片
            // 2  证件扫描件
            if (this.photoTaker === 1) {
              this.$set(this.detail, 'businessLicensePhotoId', photoId)
            } else if (this.photoTaker === 2) {
              this.$set(this.detail, 'epresentativePhotoId', photoId)
            } else if (this.photoTaker === 3) {
              this.$set(this.detail, 'epresentativePhotoId2', photoId)
            }
          } else {
            this.$toast.error(res.msg)
          }
        }, (err) => {
          this.$toast.error(err.msg)
        })
      }
    },
    // 详情数据初始化
    initDetail () {
      this.detail = JSON.parse(clientInfoDetailLocal.get())
      this.merchantName = JSON.parse(clientInfoDetailMerchantNameLocal.get())
      this.merchantName = this.merchantName.merchantName
    },
    // 预览图片
    previewImage (id) {
      // console.log(id)
      if (id) {
        this.imgUrl = id
        this.isImagePreview = true
      }
    },
    // 证件持有证件人类型
    transferRepType (type) {
      return type === 1 || type === '1' || !type ? 0 : 1
    },
    // 证件类型
    transferCerType (type) {
      return type === 1 || type === '1' || !type ? 0 : 1
    }
  },

  filters: {
    // 获取图片
    loadImage (id) {
      return id ? url + `/fms/upload/resource/thumbnail/${id}` : ''
    },
    previewLoadImage (id) {
      return id ? url + `/fms/upload/resource/${id}` : ''
    },
    // 证件持有人类型
    representativeTypeFilter (type) {
      return type === 1 ? '法人' : (type === 2 ? '经办人' : '')
    },
    // 证件类型
    certificateFilter (type) {
      return type === 1 ? '身份证' : (type === 2 ? '护照' : '')
    }
  },

  mounted () {
    this.id = JSON.parse(clientInfoDetailIDLocal.get())
    // console.log('id 2', this.id)
    if (this.id) {
      this.initDetail()
    } else {
      this.$toast.error('详情数据丢失')
    }
  }
}
</script>



